function objectSlider() {
    var swiper = new Swiper('.object .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 30,
        loop: true,
        centeredSlides: true,
        loopAdditionalSlides: 4,
        navigation: {
            nextEl: '.object .swiper-button-next',
            prevEl: '.object .swiper-button-prev',
        },
    })
}

function collectionSlider() {
    var swiper = new Swiper('.collection .swiper-container', {
        slidesPerView: 3,
        spaceBetween: 30,
        navigation: {
            nextEl: '.collection .swiper-button-next',
            prevEl: '.collection .swiper-button-prev',
        },
        pagination: {
            el: '.collection .swiper-pagination',
            type: 'custom',
            clickable: true,
            renderCustom: function (swiper, current, total) {
                return `
                    <span class="swiper-pagination-title">${current}</span>
                    <span class="swiper-pagination-title">${$(".collection .swiper-pagination").attr("data-phrase")}</span>
                    <span class="swiper-pagination-title">${total}</span>
                `
            },
        },
    })
}

let collectionSlidersArray = []

function collectionSliders() {
    $(".collection").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        collectionSlidersArray[index] = new Swiper(`.collection[data-slider-id="${index}"] .swiper-container`, {
            slidesPerView: 3,
            spaceBetween: 20,
            navigation: {
                nextEl: `.collection[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.collection[data-slider-id="${index}"] .swiper-button-prev`,
            },
            pagination: {
                el: `.collection[data-slider-id="${index}"] .swiper-pagination`,
                type: 'custom',
                clickable: true,
                renderCustom: function (swiper, current, total) {
                    return `
                    <span class="swiper-pagination-title">${current}</span>
                    <span class="swiper-pagination-title"> ${$(el).find(".swiper-pagination").attr("data-phrase")}</span>
                    <span class="swiper-pagination-title">${total}</span>
                `
                },
            },
        })
    })
}

objectSlider()
collectionSliders()
